<template>
  <v-container fill-height fluid>
    <v-row align="center" justify="center">
      <v-col>
        <v-card width="25%" class="mx-auto mt-5">
          <v-card-title>Bem vindo</v-card-title>
          <v-form ref="form" @submit.prevent="login">
            <v-card-text>
              <v-text-field
                label="E-mail"
                type="email"
                :rules="[rules.required]"
                v-model="email"
              ></v-text-field>
              <v-text-field
                label="Senha"
                :rules="[rules.required]"
                :type="showPassword ? 'text' : 'password'"
                v-model="password"
                :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                @click:append="showPassword = !showPassword"
              ></v-text-field>
            </v-card-text>
            <v-card-actions>
              <v-spacer />
              <v-btn type="submit" color="primary" :disabled="!fieldsFilled">
                Entrar
              </v-btn>
              <v-btn color="primary" @click="register">
                Criar minha conta
              </v-btn>
            </v-card-actions>
          </v-form>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "Autenticacao",
  data() {
    return {
      email: null,
      password: null,
      showPassword: false,
      rules: {
        required: (value) => !!value || "ObrigatÃ³rio",
      },
    };
  },
  computed: {
    fieldsFilled() {
      return (
        this.email != null &&
        this.email.length > 0 &&
        this.password != null &&
        this.password.length > 0
      );
    },
  },
  methods: {
    async login() {
      if (!this.$refs.form.validate()) {
        return;
      }

      const resp = await this.$axios.post("/auth/login", {
        email: this.email,
        senha: this.password,
      });

      this.handleResponse(resp);
    },
    async register() {
      if (!this.$refs.form.validate()) {
        return;
      }

      const resp = await this.$axios.post("/auth/register", {
        email: this.email,
        senha: this.password,
      });

      this.handleResponse(resp);
    },
    handleResponse(resp) {
      if (resp.data.token) {
        this.$store.dispatch("setAuthToken", resp.data.token);
        this.navigateToConfigsList();
      }
    },
    navigateToConfigsList() {
      this.$router.push({ name: "ListaConfiguracoes" });
    },
  },
};
</script>

